body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.match-modifier-btns {
  height: 30px;
  display: flex;
  justify-content: space-around;
}
.btn { 
  margin: 0 5px;
}
#match-original-game-container {
  width: 100%;
  padding: 100px 0 50px 0;
}

.matches-original {
  display: flex;
  height: 100vh;
  align-items: center;
}

.match-row {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  margin-left: auto;
  margin-right: auto;
}

.match-row:hover {
  background: #fcc;
}

.matches-fixed-width-container { 
  width: 550px;
  display: flex;
  justify-content: center;
}
#turn-action-bar {
    position: absolute;
    bottom: 0;
    width: 100%;
    margin: 30px auto;
    display: flex;
    justify-content: space-evenly;
}
#match-game-container {
    height: 100vh;
    width: 100vw;
}
.App {
  text-align: center;
}

#title-page { 
    height: 100vh;
    width: 100vw;
    text-align: center;
}

#title-page h1 { 
    text-align: center;
    width: 100%;
    padding: 30vh 0 10vh 0;
}

#title-buttons {
    display: flex;
    justify-content: space-around;
    margin: 0 10vw;
}
.route { 
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
  }
  .enter {
    opacity: 0;
}
.enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
  }
  .exit {
    opacity: 1;
  }
  .exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
  }
