.route { 
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
  }
  .enter {
    opacity: 0;
}
.enter-active {
    opacity: 1;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
  }
  .exit {
    opacity: 1;
  }
  .exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms ease-in;
    transition: opacity 300ms ease-in;
  }